@import url(https://fonts.googleapis.com/css?family=Open+Sans);
@import url(https://fonts.googleapis.com/css?family=Neuton);
:root {
  --yellow: #ffc600;
  --black: #272727;
}

html {
  /* border-box box model allows us to add padding and border to our elements without increasing their size */
  box-sizing: border-box;
  /* A system font stack so things load nice and quick! */
  font-family: 'Open Sans', Helvetica, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  color: white;
  text-shadow: 0 2px 0 rgba(0, 0, 0, 0.07);
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  background: rgb(224,30,30);
  background: -webkit-linear-gradient(bottom, rgba(40,44,52,0.8) 20%, rgba(40,44,52,0.95) 40%), -webkit-linear-gradient(bottom, rgba(224,30,30,0.76) 10%, rgba(224,30,30,0.1) 40%), url(/joeljames/static/media/collab.254efe2a.jpg) center;
  background: linear-gradient(0deg, rgba(40,44,52,0.8) 20%, rgba(40,44,52,0.95) 40%), linear-gradient(0deg, rgba(224,30,30,0.76) 10%, rgba(224,30,30,0.1) 40%), url(/joeljames/static/media/collab.254efe2a.jpg) center;
  background-size: cover;
  min-height: calc(100vh - 100px);
  margin: 50px;
  background-attachment: fixed;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0 0 5px 0;
}

div {
    display: flex;
    flex-direction: column;
    /* border: 3px solid orange; */
}

div.land-header {
    height: 100px;
    justify-content: flex-end;
}

div.land-header h3 {
    align-self: center;
    font-size: 2em;
    font-weight: 100;
    letter-spacing: 2px;
    color: white;
    text-transform: uppercase;
}

div.land-title {
    height: 300px;
    justify-content: flex-end;
    align-items: center;
}

div.land-title h1 {
    /* border: 2px solid blue; */
    color: #d63031;
    font-family: 'Neuton', serif;
    font-weight: 500;
    font-size: 5em;
    letter-spacing: 2px;
}

p.location {
    align-self: center;
    color: white;
    font-weight: 600;
    text-transform: uppercase;
    letter-spacing: 2px;
}

.bring-up-nav {
    /* border: 2px solid orange; */
    flex-direction: row;
    align-content: center;
    justify-content: center;
    height: 100px;
}

.slide-right {
    position: absolute;
    height: 70px;
    width: 70px;
    z-index: 2;
    align-self: center;
    font-size: 40px;
    /* padding: 10px 20px; */
    color: white;
    border: 1px solid white;
    padding: 12px 24px;
}

.slide-right:hover {
    position: absolute;
    background-color: rgba(224,30,30,0.2);
    height: 70px;
    width: 70px;
    z-index: 0;
    align-self: center;
    font-size: 40px;
    /* padding: 10px 20px; */
    color: white;
    border: 1px solid white;
    padding: 12px 24px;
    -webkit-transition: 1s;
    transition: 1s;
}

i.fa.fa-angle-double-right {
    position: absolute;
    height: 70px;
    width: 70px;
    /* z-index: 2; */
    align-self: center;
    font-size: 40px;
    /* padding: 10px 20px; */
    color: white;
    border: 1px solid white;
    padding: 12px 24px;
}
